import Image from "next/image";
import home_page_1 from "@/public/images/home_page_1.svg";
import home_page_2 from "@/public/images/home_page_2.svg"; // استيراد الصورة الجديدة
import home_page_4 from "@/public/images/home_page_4.svg";
import { FaSearch } from "react-icons/fa";

const Home = () => {
  return (
    <div className="relative overflow-hidden min-h-screen ">
      {/* Arka Plan */}
      <Image
        src={home_page_1}
        alt="background"
        layout="fill"
        className="object-cover z-0"
      />

      {/* İçerik */}
      <div className="relative z-20 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* صوره الخلفية */}

        <div className="absolute inset-0 z-10">
          <Image
            src={home_page_2}
            alt="foreground"
            layout="fill" // استخدم fill لجعل الصورة تأخذ حجم الحاوية
            className="object-cover" // استخدم object-cover لتغطية المساحة
          />
        </div>

        <div className="flex items-center justify-between">
          <div className="w-1/2">
            <Image
              src={home_page_4}
              alt="left image"
              layout="intrinsic" // يمكنك استخدام أي تخطيط يناسبك
              width={300}
              height={200}
              className="object-contain"
            />
          </div>
          <div className="w-1/2 text-right">
            <div className="relative w-full">
              <input
                type="text"
                placeholder="للبحث السريع عن خدماتنا..."
                dir="rtl"
                className="font-asmaa p-3 pl-12 pr-16 placeholder-gray-600 text-black 
                           bg-white border border-gray-700 rounded-xl 
                           focus:outline-none focus:ring-0 focus:border-transparent sm:text-lg"
              />
              <div className="absolute right-0 top-1/2 transform -translate-y-1/2 bg-blue-500 p-4 rounded-tr-xl rounded-br-xl flex items-center">
                <FaSearch className="text-white text-xl" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Home;
